import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { useMachine } from '@xstate/react';

// import { fetchMachine } from './machines/fetch';
import { storeMachine, initDbState } from 'src/machines/store';
import { fintrack } from 'typings/fintrack';
import { ChartsRoot } from 'src/components/charts/Charts';
import { BottomAppBar } from 'src/components/BottomAppBar';
import { AssetsAndReports } from 'src/AssetsAndReports';
import { ImportExport } from 'src/ImportExport';
import { BASE_CURRENCY } from 'src/constants';
import { ThemeProvider } from 'src/ThemeProvider';

import * as styles from './App.treat';

export const StoreContext = React.createContext<fintrack.JSONShape>(
  initDbState
);

type UserConfigType = {
  baseCurrency: fintrack.Currency;
  reportCurrencies: fintrack.Currency[];
};

export const UserConfigContext = React.createContext<UserConfigType>({
  reportCurrencies: ['USD', 'EUR', 'RUB'],
  baseCurrency: BASE_CURRENCY,
});

export const ReportCurrencyContext = React.createContext<fintrack.Currency>(
  'USD'
);

const App: React.FC = () => {
  const [state1, send, storeService] = useMachine(storeMachine, {
    devTools: true,
  });

  return (
    <StoreContext.Provider value={state1.context.db.getState()}>
      <UserConfigContext.Provider
        value={{
          baseCurrency: BASE_CURRENCY,
          reportCurrencies: ['USD', 'EUR', 'RUB'],
        }}
      >
        <BrowserRouter>
          <React.Fragment>
            <ThemeProvider>
              <div className={styles.root}>
                <div className={styles.content}>
                  <Routes>
                    <Route element={<ChartsRoot />} />
                    <Route
                      path="assets-and-reports/*"
                      element={<AssetsAndReports storeService={storeService} />}
                    />
                    <Route
                      path="import-export"
                      element={
                        <ImportExport
                          state={state1}
                          send={send}
                          storeService={storeService}
                        />
                      }
                    />
                  </Routes>
                </div>
                <BottomAppBar />
              </div>
            </ThemeProvider>
          </React.Fragment>
        </BrowserRouter>
      </UserConfigContext.Provider>
    </StoreContext.Provider>
  );
};

export default App;
