import React, { useEffect, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import * as yup from 'yup';
import { Interpreter } from 'xstate';
import { FormContextValues, useForm, Controller } from 'react-hook-form';
import { useMachine } from '@xstate/react';
import Typography from '@material-ui/core/Typography';
import { TextField, Select, MenuItem, Grid } from '@material-ui/core';

import { StoreEvent } from 'src/machines/store';
import { ALL_INSTITUTION_TYPES } from 'src/constants';
import { getAssetAccounts } from 'src/Home';
import { StoreContext } from 'src/App';
import { formMachine } from 'src/machines/form';
import { fintrack } from 'typings/fintrack';
import { ContentContainer } from 'src/components/ContentContainer';
import { SubmitButton } from 'src/components/forms/SubmitButton';
import { DeleteButton } from 'src/components/forms/DeleteButton';

type OwnProps = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

const fieldType = 'type';
const fieldName = 'name';

type FormValues = {
  [fieldType]: string;
  [fieldName]: string;
};

type FormMachineShape = {
  form: FormContextValues<FormValues>;
};

const yupInstitution = yup.object({
  [fieldType]: yup.string().required('required'),
  [fieldName]: yup.string().required('required'),
});

export const AddEditInstitution: React.FC<OwnProps> = ({ storeService }) => {
  const params = useParams();
  const store = useContext(StoreContext);
  const navigate = useNavigate();

  let inst: fintrack.Institution | undefined;
  if (params.institutionId !== undefined) {
    inst = store.data.institutions.find(i => i.id === params.institutionId);
  }

  const form = useForm<FormValues>({
    validationSchema: yupInstitution,
    defaultValues: {
      type: inst?.type,
      name: inst?.name,
    },
  });
  const [formMachineState, formMachineSend] = useMachine<FormMachineShape, any>(
    formMachine,
    {
      devTools: true,
      context: {
        form,
      },
    }
  );

  const onSubmit = (values: FormValues) => {
    console.log('________', values);
    formMachineSend('SUBMIT');
  };

  const deleteInstitution = () => {
    console.log('----delete', inst?.id);
    storeService.send({
      type: 'UPDATE_DATA',
      actionType: 'delete',
      entity: 'institutions',
      data: {
        id: inst?.id,
      },
    });
    navigate('..');
  };

  useEffect(() => {
    if (formMachineState.matches('submitted')) {
      if (inst === undefined) {
        storeService.send('UPDATE_DATA', {
          actionType: 'create',
          entity: 'institutions',
          data: {
            ...yupInstitution.validateSync(
              formMachineState.context.form.getValues()
            ),
            sortIndex: 0,
          },
        });
      } else {
        storeService.send('UPDATE_DATA', {
          actionType: 'update',
          entity: 'institutions',
          data: {
            id: inst.id,
            ...yupInstitution.validateSync(
              formMachineState.context.form.getValues()
            ),
          },
        });
      }
      formMachineSend('REINIT');
      navigate('..');
    }
  }, [formMachineState, formMachineSend, storeService, inst, navigate]);

  const assetAccountsWithCurrentInstitution = getAssetAccounts(store)!.filter(
    aa => aa.institutionId === inst?.id
  );

  return (
    <ContentContainer>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        <Grid container direction="column" spacing={3}>
          <Grid item>
            <Typography variant="h1">
              {inst?.id !== undefined ? 'Edit' : 'Add'} Institution
            </Typography>
          </Grid>
          <Grid item>
            <Grid container direction="column" spacing={2}>
              <Grid item>
                <Controller
                  as={Select}
                  name={fieldType}
                  control={form.control}
                  variant="outlined"
                  error={Boolean(form.errors[fieldType]?.message)}
                >
                  {ALL_INSTITUTION_TYPES.map(v => (
                    <MenuItem value={v}>{v}</MenuItem>
                  ))}
                </Controller>
              </Grid>
              <Grid item>
                <Controller
                  as={TextField}
                  name={fieldName}
                  control={form.control}
                  variant="outlined"
                  placeholder="Name"
                  label="Name"
                  error={Boolean(form.errors[fieldName]?.message)}
                  helperText={form.errors[fieldName]?.message}
                />
              </Grid>
            </Grid>
          </Grid>

          <Grid item>
            <SubmitButton />
          </Grid>
          {inst !== undefined && (
            <Grid item>
              <Grid container direction="column" spacing={1}>
                {assetAccountsWithCurrentInstitution.length !== 0 && (
                  <Grid item>
                    <Typography variant="body1" color="textSecondary">
                      used in {assetAccountsWithCurrentInstitution.length} of
                      asset accounts thus can not be deleted
                    </Typography>
                  </Grid>
                )}
                <Grid item>
                  <DeleteButton
                    onClick={deleteInstitution}
                    disabled={assetAccountsWithCurrentInstitution.length !== 0}
                  />
                </Grid>
              </Grid>
            </Grid>
          )}
        </Grid>
      </form>
    </ContentContainer>
  );
};
