import React, { useContext, useEffect } from 'react';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import _ from 'lodash';

import { fintrack } from 'typings/fintrack';
import {
  StoreContext,
  UserConfigContext,
  ReportCurrencyContext,
} from 'src/App';
import { getTotalForReport, getNonZeroAssetClasses } from 'src/Home';
import {
  configureTimelineChart,
  POSITIVE_PERCENT_FORMAT,
  NUMBER_FORMAT,
} from './utils';

function createData(
  store: fintrack.JSONShape,
  currency: fintrack.Currency,
  assetClasses: fintrack.ASSET_TYPE[],
  accountStateFilterFunction?: (account: fintrack.AssetAccountState) => boolean,
  valueProcessingFunction?: (t: number) => number
) {
  return _.map(store.data.reports, report => {
    const totalsPerAssetClass = assetClasses.map(assetClass => {
      return {
        assetClass: assetClass,
        totalInAssetClass: parseFloat(
          getTotalForReport(
            store,
            report,
            currency,
            a => a.class === assetClass,
            accountStateFilterFunction
          ).toFixed(0)
        ),
      };
    });
    let result: any = {
      date: new Date(report.datetime),
    };
    _.map(totalsPerAssetClass, t => {
      if (valueProcessingFunction !== undefined) {
        result[t.assetClass] = valueProcessingFunction(t.totalInAssetClass);
      } else {
        result[t.assetClass] = t.totalInAssetClass;
      }
    });
    return result;
  });
}

function createStackedChart(
  name: string,
  containerId: string,
  data: any, // TODO set type
  assetClasses: fintrack.ASSET_TYPE[],
  currency: fintrack.Currency
) {
  if (!data) {
    return null;
  }

  let chart = am4core.create(containerId, am4charts.XYChart);
  configureTimelineChart(chart, false);
  chart.data = data;

  let title = chart.titles.push(new am4core.Label());
  title.text = name;
  title.fontSize = 25;
  title.marginBottom = 15;

  let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
  valueAxis.title.text = `${currency}`;
  valueAxis.calculateTotals = true;

  // exclude 'date' key
  const seriesNames = _.filter(_.keys(data[0]), k =>
    _.includes(assetClasses, k)
  );

  const NWHistory = _.map(data, d => {
    // sum valuations in base currency for all classes in report, which is NW
    return _.sum(_.map(seriesNames, klass => d[klass]));
  });
  const hasAnyNegativeNW = _.filter(NWHistory, nw => nw < 0).length > 0;
  const hasAnyPositiveNW = _.filter(NWHistory, nw => nw > 0).length > 0;

  _.map(seriesNames, seriesName => {
    let series = chart.series.push(new am4charts.LineSeries());
    series.dataFields.dateX = 'date';
    series.dataFields.valueY = seriesName;
    series.name = seriesName;
    series.strokeWidth = 2;
    series.tooltipText = `{name}: [bold]{valueY.value.formatNumber('${NUMBER_FORMAT}')} ${currency}[/]`;
    // percents are not computed correctly when there are negative points
    if (!(hasAnyNegativeNW && hasAnyPositiveNW)) {
      series.tooltipText += ` ({valueY.totalPercent.formatNumber('${POSITIVE_PERCENT_FORMAT}')} %)`;
    }
    series.bullets.push(new am4charts.CircleBullet());
    series.fillOpacity = 0.7;
    series.stacked = true;
    series.tensionX = 0.8;
    // series.legendSettings.itemValueText = `[bold]{valueY.value} ${baseCurrency}[/]`;
    return series;
  });

  return chart;
}

export const NWValuationAllocationStackedTimelineChart: React.FC = () => {
  const containerId = 'nw_valuation_allocation_stacked_timeline_chart';
  const store = useContext(StoreContext);
  const reportCurrency = useContext(ReportCurrencyContext);

  const assetClasses = getNonZeroAssetClasses({ store });
  const data = createData(
    store,
    reportCurrency,
    assetClasses,
    accState => true
  );

  useEffect(() => {
    const chart = createStackedChart(
      'Net worth valuation allocation history',
      containerId,
      data,
      assetClasses,
      reportCurrency
    );
    return () => {
      chart?.dispose();
    };
  }, [data, assetClasses, reportCurrency, containerId]);

  return (
    <div
      id={containerId}
      style={{ width: '100%', height: '500px', marginBottom: '100px' }}
    ></div>
  );
};

export const AssetsValuationAllocationStackedTimelineChart: React.FC = () => {
  const containerId = 'assets_percentage_allocation_stacked_timeline_chart';
  const store = useContext(StoreContext);
  const reportCurrency = useContext(ReportCurrencyContext);

  const assetClasses = getNonZeroAssetClasses({ store: store, mode: 'assets' });
  const data = createData(
    store,
    reportCurrency,
    assetClasses,
    accState => parseFloat(accState.balance) >= 0
  );

  useEffect(() => {
    const chart = createStackedChart(
      'Assets valuation allocation history',
      containerId,
      data,
      assetClasses,
      reportCurrency
    );
    return () => {
      chart?.dispose();
    };
  }, [reportCurrency, data, containerId, assetClasses]);

  return (
    <div
      id={containerId}
      style={{ width: '100%', height: '500px', marginBottom: '100px' }}
    ></div>
  );
};

// TODO: unify with AssetsPercentageAllocationStackedTimelineChart
export const LiabilitiesValuationAllocationStackedTimelineChart: React.FC = () => {
  const containerId =
    'liabilities_percentage_allocation_stacked_timeline_chart';
  const store = useContext(StoreContext);
  const reportCurrency = useContext(ReportCurrencyContext);

  const assetClasses = getNonZeroAssetClasses({
    store: store,
    mode: 'liabilities',
  });
  const data = createData(
    store,
    reportCurrency,
    assetClasses,
    accState => parseFloat(accState.balance) < 0,
    Math.abs // used to show liabilities in positive values
  );

  useEffect(() => {
    const chart = createStackedChart(
      'Liabilities valuation allocation history',
      containerId,
      data,
      assetClasses,
      reportCurrency
    );
    return () => {
      chart?.dispose();
    };
  }, [reportCurrency, data, containerId, assetClasses]);

  return (
    <div
      id={containerId}
      style={{ width: '100%', height: '500px', marginBottom: '100px' }}
    ></div>
  );
};

