import { fintrack } from 'typings/fintrack';
import { ACCOUNTING_MONEY_FORMAT } from 'src/constants';
import accounting from 'accounting';

export function formatMoney(
  amount: string,
  symbol: string = '',
  format: fintrack.AccountingMoneyFormat = ACCOUNTING_MONEY_FORMAT
) {
  return accounting.formatMoney(amount, symbol, ...format);
}
