function createChart(
  containerId: string,
  currency: fintrack.Currency,
  data: ValuationsInBaseCurrencyPerClass[]
) {
  let chart = am4core.create(containerId, am4charts.XYChart);
  chart.data = data;

  const hasLiabilities = _.sum(_.map(data, el => el.valuationLiabilities || 0));

  // Title
  let title = chart.titles.push(new am4core.Label());
  title.text = 'Current net worth valuation allocation';
  title.fontSize = 25;
  title.marginBottom = 15;

  // Create axes
  let categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
  categoryAxis.dataFields.category = 'class';

  categoryAxis.renderer.inversed = true;
  categoryAxis.renderer.minGridDistance = 20;
  categoryAxis.renderer.grid.template.location = 0;
  categoryAxis.renderer.axisFills.template.disabled = false;
  categoryAxis.renderer.axisFills.template.fillOpacity = 0.05;

  categoryAxis.renderer.cellStartLocation = 0.05;
  categoryAxis.renderer.cellEndLocation = 0.95;

  let valueAxis = chart.xAxes.push(new am4charts.ValueAxis());
  valueAxis.renderer.minGridDistance = 50;
  valueAxis.renderer.ticks.template.length = 5;
  valueAxis.renderer.ticks.template.disabled = false;
  valueAxis.renderer.ticks.template.strokeOpacity = 0.4;
  valueAxis.extraMax = 0.1;
  if (hasLiabilities) {
    valueAxis.extraMin = 0.1;
  }

  // Use positive X-axis values for liabilities
  chart.numberFormatter.numberFormat = '#.#s';

  // Create series
  function createSeries(field: string, name: string, color: any) {
    let series = chart.series.push(new am4charts.ColumnSeries());
    series.dataFields.valueX = field;
    series.dataFields.categoryY = 'class';
    series.name = name;
    series.stroke = color;
    series.fill = color;
    if (field !== 'valuationTotal') {
      series.stacked = true;
    }
    if (field === 'valuationAssets') {
      series.columns.template.column.cornerRadiusBottomRight = 5;
      series.columns.template.column.cornerRadiusTopRight = 5;
    } else {
      series.columns.template.column.cornerRadiusBottomLeft = 5;
      series.columns.template.column.cornerRadiusTopLeft = 5;
    }

    series.columns.template.tooltipText = `{name}: [bold]{valueX.value.formatNumber('${POSITIVE_NUMBER_FORMAT}')} ${currency}[/]`;

    const valueLabel = series.bullets.push(new am4charts.LabelBullet());
    valueLabel.label.text = `{name}: [bold]{valueX.value.formatNumber('${POSITIVE_NUMBER_FORMAT}')} ${currency}[/]`;
    if (field === 'valuationAssets') {
      valueLabel.label.horizontalCenter = 'right';
      valueLabel.label.dx = -10;
    } else {
      valueLabel.label.horizontalCenter = 'left';
      valueLabel.label.dx = 10;
    }
    valueLabel.label.hideOversized = true;
    valueLabel.label.truncate = false;

    // Hide labels with 0 value
    // valueLabel.label.adapter.add('textOutput', (text, target) => {
    //   if (target.dataItem && target.dataItem.values.valueX.value == 0) {
    //     return '';
    //   }
    //   return text;
    // });

    // const categoryLabel = series.bullets.push(new am4charts.LabelBullet());
    // categoryLabel.label.text = "{name}";
    // categoryLabel.label.horizontalCenter = "right";
    // categoryLabel.label.dx = -10;
    // // categoryLabel.label.fill = am4core.color("#fff");
    // categoryLabel.label.hideOversized = true;
    // categoryLabel.label.truncate = false;
  }

  let interfaceColors = new am4core.InterfaceColorSet();
  let positiveColor = interfaceColors.getFor('positive');
  let negativeColor = interfaceColors.getFor('negative');

  createSeries('valuationAssets', 'Assets', positiveColor);
  if (hasLiabilities) {
    createSeries('valuationLiabilities', 'Liabilities', negativeColor);
    // No reason to show legend if there are only one series in chart (assets).
    chart.legend = new am4charts.Legend();
    chart.legend.position = 'bottom';
  }

  chart.paddingRight = 100;
  return chart;
}

export const NWValuationAllocationChart: React.FC = () => {
  const containerId = 'nw_valuation_allocation_chart';
  const store = useContext(StoreContext);
  // const userConfig = useContext(UserConfigContext);
  const reportCurrency = useContext(ReportCurrencyContext);

  const lastReport = _.maxBy(store.data.reports, r => r.datetime);

  let data: ValuationsInBaseCurrencyPerClass[] = [];
  if (lastReport) {
    // const nonEmptyAccounts = _.filter(store.data.assetAccounts, acc => true);
    // const assetIds = _.map(nonEmptyAccounts, acc => acc.assetId);
    // const uniqueClasses = _.uniq(
    //   _.map(assetIds, assetId => {
    //     return store.data.assets.find(a => a.id === assetId)!.class;
    //   })
    // );
    const assetClasses = getNonZeroAssetClasses({
      store: store,
      report: lastReport,
    });
    data = _.map(assetClasses, klass => {
      let r = {
        class: klass,
        valuationAssets: parseFloat(
          getTotalForReport(
            store,
            lastReport,
            reportCurrency,
            a => a.class === klass,
            accState => parseFloat(accState.balance) >= 0
          ).valueOf()
        ),
        valuationLiabilities: parseFloat(
          getTotalForReport(
            store,
            lastReport,
            reportCurrency,
            a => a.class === klass,
            accState => parseFloat(accState.balance) < 0
          ).valueOf()
        ),
        // valuationTotal: parseFloat(
        //   getTotalForReport(
        //     store,
        //     lastReport,
        //     baseCurrency,
        //     a => a.class === klass
        //   ).valueOf()
        // ),
      };
      // delete column from chart completely.
      if (r.valuationLiabilities == 0) {
        delete r.valuationLiabilities;
      }
      return r;
    });
  }

  useEffect(() => {
    const chart = createChart(containerId, reportCurrency, data);
    return () => {
      chart.dispose();
    };
  }, [containerId, reportCurrency, data]);

  return <div id={containerId} style={{ height: '400px' }} />;
};
