import React, { useContext, useEffect } from 'react';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import _ from 'lodash';

import {
  StoreContext,
  UserConfigContext,
  ReportCurrencyContext,
} from 'src/App';
import { fintrack } from 'typings/fintrack';
import {
  getTotalForReport,
  getNonZeroAssetClasses,
  getNonZeroAssetClassNames,
} from 'src/Home';
import { NUMBER_FORMAT, PERCENT_FORMAT } from './utils';
import { last } from '@amcharts/amcharts4/.internal/core/utils/Array';

export type TotalInBaseCurrencyPerAssetName = {
  assetClass: fintrack.ASSET_TYPE;
  assetName: string;
  totalInBaseCurrency: string;
};

function createChart(
  title: string,
  containerId: string,
  baseCurrency: fintrack.Currency,
  data: TotalInBaseCurrencyPerAssetName[]
) {
  let chart = am4core.create(containerId, am4charts.PieChart);
  chart.data = data;

  // Title
  let chartTitle = chart.titles.push(new am4core.Label());
  chartTitle.text = title;
  chartTitle.fontSize = 20;
  // chartTitle.marginBottom = 15;

  chart.innerRadius = am4core.percent(30);

  let series = chart.series.push(new am4charts.PieSeries());
  series.dataFields.value = 'totalInBaseCurrency';
  series.dataFields.category = 'assetName';

  // NOTE: to move labels inside pie slices
  // series.ticks.template.disabled = true;
  // series.alignLabels = false;
  // series.labels.template.radius = am4core.percent(-35);
  // series.labels.template.fill = am4core.color('white');

  series.slices.template.strokeWidth = 2;
  series.slices.template.stroke = am4core.color('#000');
  series.slices.template.tooltipText = `{value.value.formatNumber('${NUMBER_FORMAT}')} ${baseCurrency}`;

  series.labels.template.text = `{category}: {value.percent.formatNumber('${PERCENT_FORMAT}')}%`;
  return chart;
}

export const AssetClassValuationAllocationPieCharts: React.FC = () => {
  const store = useContext(StoreContext);

  const lastReport = _.maxBy(store.data.reports, r => r.datetime);
  if (lastReport) {
    const assetClasses = getNonZeroAssetClasses({
      store: store,
      report: lastReport,
      mode: 'assets',
    });
    return (
      <React.Fragment>
        {_.map(assetClasses, (assetClass: fintrack.ASSET_TYPE) => {
          return (
            <AssetClassValuationAllocationChart
              key={assetClass}
              assetClass={assetClass}
            />
          );
        })}
      </React.Fragment>
    );
  }
  return null;
};

const AssetClassValuationAllocationChart: React.FC<{
  assetClass: fintrack.ASSET_TYPE;
}> = ({ assetClass }) => {
  const containerId = `asset_class_${assetClass}_nw_dist_by_name_chart`;
  const store = useContext(StoreContext);
  const reportCurrency = useContext(ReportCurrencyContext);

  const lastReport = _.maxBy(store.data.reports, r => r.datetime);

  let data: TotalInBaseCurrencyPerAssetName[] = [];
  if (lastReport) {
    const names = getNonZeroAssetClassNames({
      store: store,
      report: lastReport,
      assetClass: assetClass,
    });
    data = names.map(assetName => {
      return {
        assetClass: assetClass,
        assetName: assetName,
        totalInBaseCurrency: getTotalForReport(
          store,
          lastReport,
          reportCurrency,
          (a: fintrack.Asset) => a.class === assetClass && a.name === assetName
        ).toFixed(0),
      };
    });
  }

  useEffect(() => {
    const chart = createChart(
      `[bold]${assetClass}[/] allocation`,
      containerId,
      reportCurrency,
      data
    );
    return () => {
      chart.dispose();
    };
  }, [containerId, reportCurrency, data]);

  return (
    <div style={{ width: '50%', height: '200px' }}>
      <div id={containerId} style={{ height: '200px' }} />
    </div>
  );
};
