import React from 'react';
import Button from '@material-ui/core/Button';
import { DeleteForever as DeleteForeverIcon } from '@material-ui/icons';

export const DeleteButton: React.FC<{
  onClick: () => void;
  disabled?: boolean;
}> = ({ onClick, disabled }) => {
  return (
    <Button
      variant="contained"
      color="secondary"
      startIcon={<DeleteForeverIcon />}
      onClick={onClick}
      disabled={disabled}
    >
      Delete
    </Button>
  );
};
