import _ from 'lodash';
import React, { useContext } from 'react';
import { Interpreter } from 'xstate';
import { useForm, Controller } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import { IconButton } from '@material-ui/core';
import { Restore as RestoreIcon } from '@material-ui/icons';
import * as yup from 'yup';
import { useNavigate } from 'react-router';

import { StoreContext } from 'src/App';
import { fintrack } from 'typings/fintrack';
import { StoreEvent } from 'src/machines/store';
import { Table, Tr, Td } from 'src/components/Table';
import { getAssetAccounts, getInstitutions } from 'src/Home';
import { formatMoney } from 'src/utils';
import { SubmitButton } from 'src/components/forms/SubmitButton';

type OwnProps = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

type FormValues = {
  date: string;
  assetAccountStates: fintrack.AssetAccountState[];
  exchangeRates: fintrack.ExchangeRateData;
};

export const AddEditReport: React.FC<OwnProps> = ({ storeService }) => {
  const store = useContext(StoreContext);
  const params = useParams();

  const assetAccounts = getAssetAccounts(store);

  let report: fintrack.Report | undefined;
  let assetAccountStates: fintrack.AssetAccountState[] | undefined;
  let exchangeRates: fintrack.ExchangeRateData = {};
  if (params.reportId !== undefined) {
    report = store.data.reports.find(r => r.id === params.reportId);
    const nonSortedAssetAccountStates = store.data.assetAccountStates.filter(
      r => r.reportId === report?.id
    );
    const exchangeRate = store.data.exchangeRates.find(
      er => er.reportId === report?.id
    );
    exchangeRates = exchangeRate!.data;
    assetAccountStates = assetAccounts?.map(aa => {
      const foundAas = nonSortedAssetAccountStates?.find(
        aas => aas.assetAccountId === aa.id
      );
      if (foundAas !== undefined) {
        return foundAas;
      }
      return {
        assetAccountId: aa.id,
        reportId: report?.id!,
        balance: '0',
        comment: '',
      };
    });
  }

  const assetIdsSchema = assetAccounts!.reduce((acc, aa) => {
    return {
      ...acc,
      [aa.assetId]: yup.string().required(' , '),
    };
  }, {});

  const yupAssectAccountState = yup.object({
    date: yup.date().required('!'),
    assetAccountStates: yup.array().of(
      yup.object({
        balance: yup.string().required(', '),
        assetAccountId: yup.string().required(', '),
        // pricePerUnit: yup.string().required(', '),
      })
    ),
    exchangeRates: yup.object(assetIdsSchema),
  });

  const form = useForm<FormValues>({
    // validationResolver: superstructResolver(StructAssectAccountState),
    // validationResolver: yupResolver(yupAssectAccountState),
    validationSchema: yupAssectAccountState,
    defaultValues: {
      date: report?.datetime
        ? new Date(report.datetime).toISOString().substr(0, 10)
        : new Date().toISOString().substr(0, 10),
      assetAccountStates,
      exchangeRates,
    },
    // validationContext: {
    //   'x': 'asd'
    // }
  });

  const navigate = useNavigate();

  const nowTimestmap = new Date().getTime();
  const prevReport = _.maxBy(
    _.filter(
      store.data.reports,
      r => r.datetime < (report ? report.datetime : nowTimestmap)
    ),
    r => r.datetime
  );
  const prevAssetAccountStates = store.data.assetAccountStates.filter(
    aas => aas.reportId === prevReport?.id
  );
  const prevExRates = store.data.exchangeRates.find(
    exr => exr.reportId === prevReport?.id
  );

  const onSubmit = (values: FormValues) => {
    console.log('=====values', values.date, values);
    if (report === undefined) {
      storeService.send({
        type: 'UPDATE_DATA',
        actionType: 'create',
        entity: 'assetAccountStates',
        data: values,
      });
    } else {
      storeService.send({
        type: 'UPDATE_DATA',
        actionType: 'update',
        entity: 'assetAccountStates',
        data: {
          reportId: report.id,
          ...values,
        },
      });
    }
    navigate('/assets-and-reports');
  };

  console.log('-----form.errors', form.errors);

  return (
    <div>
      <h1>Add report</h1>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        <input type="date" name="date" ref={form.register} />
        {form.errors.date?.message}
        <br />
        <br />

        <Table>
          <tbody>
            {assetAccounts?.map((assetAccount, index) => {
              const asset = store.data.assets.find(
                aa => aa.id === assetAccount.assetId
              )!;

              const renderInstitutionHeader =
                index === 0 ||
                assetAccounts[index].institutionId !==
                  assetAccounts[index - 1].institutionId;

              const rowWithHeader = !renderInstitutionHeader ? null : (
                <Tr key={`header${assetAccount.id}`}>
                  <Td colspan={2}>
                    <h3>
                      {
                        getInstitutions(store)?.find(
                          i => i.id === assetAccount.institutionId
                        )?.name
                      }
                    </h3>
                  </Td>
                </Tr>
              );

              const prevAssetAccountBalance: string =
                prevAssetAccountStates?.find(
                  laas => laas.assetAccountId === assetAccount.id
                )?.balance || '0';

              const prevAssetExRate: string =
                prevExRates?.data[asset.id] || 'none';

              const exchangeRateForThisAssetWasAbove =
                assetAccounts
                  ?.slice(0, index)
                  .find(aa => aa.assetId === assetAccount.assetId) !==
                undefined;

              const watchExchangeRate =
                exchangeRateForThisAssetWasAbove && form.watch('exchangeRates');

              const rowWithData = (
                <Tr key={`data${assetAccount.id}`}>
                  <Td>
                    <Controller
                      as={TextField}
                      label={assetAccount.name}
                      InputLabelProps={{
                        shrink: true,
                        disableAnimation: true,
                      }}
                      InputProps={{
                        endAdornment: (
                          <IconButton
                            aria-label="use last balance"
                            color="secondary"
                            size="small"
                            onClick={() =>
                              form.setValue(
                                `assetAccountStates[${index}].balance`,
                                prevAssetAccountBalance
                              )
                            }
                          >
                            <RestoreIcon />
                          </IconButton>
                        ),
                      }}
                      name={`assetAccountStates[${index}].balance`}
                      control={form.control}
                      variant="outlined"
                      helperText={
                        form.errors.assetAccountStates?.[index]?.balance !==
                        undefined
                          ? form.errors.assetAccountStates?.[index]?.balance!
                              .message
                          : `last balance: ${formatMoney(
                              prevAssetAccountBalance
                            )}`
                      }
                      error={Boolean(
                        form.errors.assetAccountStates?.[index]?.balance
                      )}
                    />
                    <input
                      type="hidden"
                      name={`assetAccountStates[${index}].assetAccountId`}
                      ref={form.register}
                      value={assetAccount.id}
                    />
                  </Td>
                  <Td>
                    {!exchangeRateForThisAssetWasAbove ? (
                      <Controller
                        as={TextField}
                        InputLabelProps={{
                          shrink: true,
                          disableAnimation: true,
                        }}
                        name={`exchangeRates[${assetAccount.assetId}]`}
                        label={`1 ${asset.name} in ${asset.currency}`}
                        control={form.control}
                        variant="outlined"
                        // WTF any here
                        // https://github.com/react-hook-form/react-hook-form/issues/987
                        helperText={
                          (form.errors.exchangeRates as any)?.[
                            assetAccount.assetId
                          ] !== undefined
                            ? (form.errors.exchangeRates as any)?.[
                                assetAccount.assetId
                              ].message
                            : `last ex. rate: ${prevAssetExRate}`
                        }
                        error={Boolean(
                          (form.errors.exchangeRates as any)?.[
                            assetAccount.assetId
                          ]
                        )}
                      />
                    ) : watchExchangeRate === false ? null : (
                      watchExchangeRate[assetAccount.assetId]
                    )}
                  </Td>
                </Tr>
              );
              return (
                <React.Fragment key={assetAccount.id}>
                  {rowWithHeader}
                  {rowWithData}
                </React.Fragment>
              );
            })}
          </tbody>
        </Table>

        <SubmitButton />
      </form>
    </div>
  );
};
