import { Machine, assign, spawn, send } from 'xstate';
import { customAlphabet } from 'nanoid';
import low, { LowdbSync } from 'lowdb';
import Memory from 'lowdb/adapters/Memory';

import { fetchMachine } from 'src/machines/fetch';
import { fintrack } from 'typings/fintrack';

const nonAmbiquousChars = 'ABCDEFGHJKLMNPQRSTUVWXYZ123456789';
const nanoid = customAlphabet(nonAmbiquousChars, 6);

// TODO should not be exported from here and leak state
export const initDbState: fintrack.JSONShape = {
  settings: {
    alphaVantageToken: '',
  },
  data: {
    institutions: [],
    assets: [],
    assetAccounts: [],
    assetAccountStates: [],
    reports: [],
    exchangeRates: [],
  },
};

const sessionStorageVarName = 'fintrack_db';

const localDateStringToUtcTimestamp = (date: string): number => {
  const localDate = new Date(date);
  const utcTimestamp = Date.UTC(
    localDate.getFullYear(),
    localDate.getMonth(),
    localDate.getDate()
  );
  return utcTimestamp;
};

interface StoreSchema {
  states: {
    init: {};
    idle: {};
    loaded: {};
    loaded_from_input: {};
    saving: {};
    updating_data: {};
    // saved: {};
  };
}

export interface StoreContext {
  db: LowdbSync<fintrack.JSONShape>;
  // data: fintrack.JSONShape | null;
  // fetchMachine: StateMachine<FetchContext, FetchSchema, FetchEvent, any> | null;
  fetchRef: any;
  urlDataToSave: string | undefined;
}

type EventUpdateDataBase = {
  type: 'UPDATE_DATA';
  actionType: 'delete' | 'create' | 'update';
};

type EventUpdateDataCreateCommon = {
  entity: 'institutions' | 'assets' | 'assetAccounts' | 'reports';
  data: any;
};

type EventUpdateDataCreateAssetAccountStates = {
  entity: 'assetAccountStates';
  actionType: 'create';
  data: {
    date: string;
    assetAccountStates: fintrack.AssetAccountState[];
    exchangeRates: fintrack.ExchangeRateData;
  };
};

type EventUpdateDataUpdateAssetAccountStates = {
  entity: 'assetAccountStates';
  actionType: 'update';
  data: {
    reportId: fintrack.ID;
    date: string;
    assetAccountStates: fintrack.AssetAccountState[];
    exchangeRates: fintrack.ExchangeRateData;
  };
};

// type EventUpdateDataUpdateExchangeRate = {
//   entity: 'exchangeRates';
//   actionType: 'update';
//   data: {
//     asset: fintrack.Asset;
//     report: fintrack.Report;
//     value: string;
//     // date: Date;
//     // assetAccountStates: fintrack.AssetAccountState[];
//   };
// };

type EventUpdateData =
  | (EventUpdateDataBase & EventUpdateDataCreateCommon)
  | (EventUpdateDataBase & EventUpdateDataCreateAssetAccountStates)
  | (EventUpdateDataBase & EventUpdateDataUpdateAssetAccountStates);
// | (EventUpdateDataBase & EventUpdateDataUpdateExchangeRate);

type LoadFromInput = {
  type: 'LOAD_FROM_INPUT';
  data: fintrack.JSONShape;
};

export type StoreEvent =
  | { type: 'LOAD'; data: fintrack.JSONShape }
  | LoadFromInput
  | {
      type: 'LOADED';
    }
  | { type: 'SAVE' }
  | { type: 'SAVED' }
  | EventUpdateData;

// {
//   context: {
//     url: shouldFetchFail ? 'https://non-existent-url' : '/data.json',
//     maxRetries: 4,
//   },
// }

const initDb = low(new Memory<fintrack.JSONShape>(''));
initDb.setState(initDbState);

export const storeMachine = Machine<StoreContext, StoreSchema, StoreEvent>({
  key: 'store',
  initial: 'init',
  context: {
    db: initDb,
    // data: null,
    urlDataToSave: undefined,
    // fetchMachine: null,
    fetchRef: null,
  },
  entry: assign({
    fetchRef: () =>
      spawn(
        fetchMachine.withContext({
          url: '/data.json',
          maxRetries: 4,
          data: null,
          error: null,
          retries: 0,
        }),
        {
          name: 'fetch',
        }
      ),
    // TODO any
  }) as any,
  states: {
    init: {
      entry: assign((ctx, e) => {
        const ssDbState = sessionStorage.getItem(sessionStorageVarName);
        if (ssDbState === null) {
          return {};
        }
        const dbData: fintrack.JSONShape = JSON.parse(ssDbState);
        const db = low(new Memory<fintrack.JSONShape>(''));
        db.setState(dbData);
        return {
          db,
        };
      }),
      on: {
        '': 'idle',
      },
    },
    idle: {
      on: {
        LOAD: {
          actions: send('FETCH', {
            to: 'fetch',
          }),
        },
        LOADED: 'loaded',
        UPDATE_DATA: 'updating_data',
        LOAD_FROM_INPUT: 'loaded_from_input',
      },
    },
    loaded: {
      entry: assign((ctx, e) => {
        if (ctx.urlDataToSave !== undefined) {
          window.URL.revokeObjectURL(ctx.urlDataToSave);
        }
        const db = low(new Memory<fintrack.JSONShape>(''));
        db.setState(ctx.fetchRef._state.context.data);
        // console.log(
        //   '=====ctx.fetchRef._state.context.data',
        //   ctx.fetchRef._state.context.data,
        //   db.getState()
        // );
        return {
          db,
          // data: ctx.fetchRef._state.context.data,
          urlDataToSave: undefined,
        };
      }),
      on: {
        SAVE: 'saving',
        UPDATE_DATA: 'updating_data',
      },
    },
    loaded_from_input: {
      entry: assign((ctx, e: LoadFromInput) => {
        console.log('=========loaded_from_input', e);
        // if (ctx.urlDataToSave !== undefined) {
        //   window.URL.revokeObjectURL(ctx.urlDataToSave);
        // }
        const db = low(new Memory<fintrack.JSONShape>(''));
        db.setState(e.data);
        // console.log(
        //   '=====ctx.fetchRef._state.context.data',
        //   ctx.fetchRef._state.context.data,
        //   db.getState()
        // );
        sessionStorage.setItem(sessionStorageVarName, JSON.stringify(e.data));
        return {
          db,
          // data: ctx.fetchRef._state.context.data,
          urlDataToSave: undefined,
        };
      }),
      on: {
        SAVE: 'saving',
        UPDATE_DATA: 'updating_data',
        LOAD_FROM_INPUT: 'loaded_from_input',
      },
    },
    saving: {
      entry: assign((ctx, e) => {
        return {
          urlDataToSave: window.URL.createObjectURL(
            new Blob([JSON.stringify(ctx.db.getState(), null, 2)], {
              type: 'text/plain',
            })
          ),
        };
      }),
      on: {
        SAVED: 'loaded',
      },
      // invoke: {
      //   src: 'saver',
      //   onDone: 'saved',
      //   onError: 'saved',
      // }
    },
    updating_data: {
      entry: assign((ctx, e: EventUpdateData) => {
        if (e.actionType === 'create') {
          if (e.entity === 'assetAccountStates') {
            const reportId = nanoid();
            ctx.db
              .get('data')
              .get('reports')
              .push({
                id: reportId,
                datetime: localDateStringToUtcTimestamp(e.data.date),
              })
              .write();
            e.data.assetAccountStates.forEach(aas => {
              ctx.db
                .get('data')
                .get('assetAccountStates')
                .push({
                  ...aas,
                  reportId,
                })
                .write();
            });
            ctx.db
              .get('data')
              .get('exchangeRates')
              .push({
                id: nanoid(),
                reportId,
                data: e.data.exchangeRates,
              })
              .write();
          } else {
            ctx.db
              .get('data')
              ?.get(e.entity)
              .push({
                id: nanoid(),
                ...e.data,
              })
              .write();
          }
        } else if (e.actionType === 'update') {
          // if (e.entity === 'exchangeRates') {
          // const allErs = ctx.db.get('data').get('exchangeRates');
          // const er = allErs?.find(ex => ex.reportId === e.data.report.id);
          // if (er?.value() !== undefined) {
          //   er.get('data').set(e.data.asset.id, e.data.value).write();
          // } else {
          //   const newEr: fintrack.ExchangeRate = {
          //     id: nanoid(),
          //     reportId: e.data.report.id,
          //     data: {
          //       [e.data.asset.id]: e.data.value,
          //     },
          //   };
          //   allErs?.push(newEr).write();
          // }
          // }
          if (e.entity === 'assetAccountStates') {
            const allReports = ctx.db.get('data').get('reports');
            const report = allReports?.find(r => r.id === e.data.reportId);
            report
              ?.set('datetime', localDateStringToUtcTimestamp(e.data.date))
              .write();

            let updatedAssetAccountStates: fintrack.ID[] = [];

            ctx.db
              .get('data')
              .get('assetAccountStates')
              .forEach(aas => {
                if (aas.reportId === e.data.reportId) {
                  aas.balance = e.data.assetAccountStates.find(
                    aasInner => aasInner.assetAccountId === aas.assetAccountId
                  )?.balance!;
                  updatedAssetAccountStates.push(aas.assetAccountId);
                }
              })
              .write();

            e.data.assetAccountStates
              .filter(
                aas => !updatedAssetAccountStates.includes(aas.assetAccountId)
              )
              .forEach(newAas => {
                ctx.db
                  .get('data')
                  .get('assetAccountStates')
                  .push({
                    ...newAas,
                    reportId: e.data.reportId,
                  })
                  .write();
              });

            ctx.db
              .get('data')
              .get('exchangeRates')
              .find(er => er.reportId === e.data.reportId)
              .set('data', e.data.exchangeRates)
              .write();

            // e.data.exchangesRates;
          }
          if (e.entity === 'institutions') {
            const allInsts = ctx.db.get('data').get('institutions');
            const inst = allInsts?.find(i => i.id === e.data.id);
            inst?.assign(e.data).write();
          }
        } else if (e.actionType === 'delete') {
          if (e.entity === 'institutions') {
            ctx.db
              .get('data')
              .get('institutions')
              .remove(i => i.id === e.data.id)
              .write();
          }
          if (e.entity === 'assets') {
            ctx.db
              .get('data')
              .get('assets')
              .remove(i => i.id === e.data.id)
              .write();
          }
          if (e.entity === 'assetAccounts') {
            ctx.db
              .get('data')
              .get('assetAccounts')
              .remove(i => i.id === e.data.id)
              .write();
          }
          if (e.entity === 'reports') {
            ctx.db
              .get('data')
              .get('reports')
              .remove(i => i.id === e.data.id)
              .write();
            ctx.db
              .get('data')
              .get('assetAccountStates')
              .remove(i => i.reportId === e.data.id)
              .write();
            ctx.db
              .get('data')
              .get('exchangeRates')
              .remove(i => i.reportId === e.data.id)
              .write();
          }
        } else {
          throw new Error('How did you end up here?');
        }
        sessionStorage.setItem(
          sessionStorageVarName,
          JSON.stringify(ctx.db.getState())
        );
        // ctx.data?.institutions.push({
        //   id: nanoid(),
        //   ...e.data,
        // });
        // console.log(
        //   '!!!!!!updating_data',
        //   e.actionType,
        //   e.type,
        //   e.data,
        //   ctx.db.getState()
        // );
        return {};
      }),
      on: {
        '': 'idle',
      },
    },
  },
  // {
  //   services: {
  //     saver: context => {
  //       var data = new Blob(['asd'], {type: 'text/plain'});
  //       // If we are replacing a previously generated file we need to
  //       // manually revoke the object URL to avoid memory leaks.
  //       // if (textFile !== null) {
  //       //   window.URL.revokeObjectURL(textFile);
  //       // }
  //       // textFile = window.URL.createObjectURL(data);

  //       // returns a URL you can use as a href
  //       return window.URL.createObjectURL(data);
  //     }
  //   }
  // }
});
