import React, { useContext, useState } from 'react';
import _ from 'lodash';
import * as am4core from '@amcharts/amcharts4/core';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import am4themes_material from '@amcharts/amcharts4/themes/material';

import { hadLiabilities, getNonZeroAssetClasses } from 'src/Home';
import {
  StoreContext,
  UserConfigContext,
  ReportCurrencyContext,
} from 'src/App';
import { NWDistByAssetClassTimelineChart } from './NWDistByAssetClassTimelineChart';
import { NWInCurrenciesTimelineChart } from './NWValuationInCurrenciesTimelineChart';
import { NWDiffInCurrenciesTimelineChart } from './NWValuationDiffInCurrenciesTimelineChart';
import { NWValuationAllocationChart } from './NWValuationAllocationChart';
import { AssetClassValuationAllocationPieCharts } from './AssetClassValuationAllocationPieCharts';
import {
  NWValuationAllocationStackedTimelineChart,
  AssetsValuationAllocationStackedTimelineChart,
  LiabilitiesValuationAllocationStackedTimelineChart,
} from './AllocationTimelineCharts';
import { fintrack } from 'typings/fintrack';

am4core.useTheme(am4themes_animated);
am4core.useTheme(am4themes_material);

export const ChartsRoot: React.FC = () => {
  const userConfig = useContext(UserConfigContext);
  const defaultReportCurreny = userConfig.reportCurrencies[0];
  const [reportCurrency, setReportCurrency] = useState(defaultReportCurreny);

  return (
    <div>
      <form>
        <label>
          Select report currency:
          <select
            value={reportCurrency}
            onChange={e =>
              setReportCurrency(e.target.value as fintrack.Currency)
            }
          >
            <React.Fragment>
              {_.map(userConfig.reportCurrencies, cur => {
                return (
                  <option key={cur} value={cur}>
                    {cur}
                  </option>
                );
              })}
            </React.Fragment>
          </select>
        </label>
      </form>
      <ReportCurrencyContext.Provider value={reportCurrency}>
        <Charts />
      </ReportCurrencyContext.Provider>
    </div>
  );
};

export const Charts: React.FC = () => {
  const store = useContext(StoreContext);
  const assetClassesNum = getNonZeroAssetClasses({ store: store }).length;
  const assetsNum = getNonZeroAssetClasses({ store: store, mode: 'assets' })
    .length;
  const liabilitiesNum = getNonZeroAssetClasses({
    store: store,
    mode: 'liabilities',
  }).length;
  // Allocation is always 100% if there is only one class, so no reason to show
  // this chart in this case (it would duplicate NWInCurrenciesTimelineChart)..
  const nwValuationAllocationTimelineBlock =
    assetClassesNum > 1 ? (
      <div
        style={{
          width: '100%',
          height: 300,
          display: 'flex',
          marginBottom: '300px',
        }}
      >
        <div style={{ width: '100%', height: 300 }}>
          <NWValuationAllocationStackedTimelineChart />
        </div>
      </div>
    ) : null;

  // const assetsAndLiabilitiesPresence = [
  //   getNonZeroAssetClasses({ store: store, mode: 'assets' }).length > 1,
  //   getNonZeroAssetClasses({ store: store, mode: 'liabilities' }).length > 1,
  // ];

  const assetsOrLiabilitiesAllocationTimelineBlockStyle = {
    width: '100%',
    height: 300,
    display: 'flex',
    marginBottom: '300px',
  };

  const assetsAllocationTimelineBlock =
    assetsNum && liabilitiesNum ? (
      <div style={assetsOrLiabilitiesAllocationTimelineBlockStyle}>
        <div style={{ width: '100%', height: 300 }}>
          <AssetsValuationAllocationStackedTimelineChart />
        </div>
      </div>
    ) : null;

  const liabilitiesAllocationTimelineBlock = liabilitiesNum ? (
    <div style={assetsOrLiabilitiesAllocationTimelineBlockStyle}>
      <div style={{ width: '100%', height: 300 }}>
        <LiabilitiesValuationAllocationStackedTimelineChart />
      </div>
    </div>
  ) : null;

  return (
    <div>
      <h1>Charts</h1>
      <div
        style={{
          width: '100%',
          height: 300,
          display: 'flex',
          marginBottom: '300px',
        }}
      >
        <div style={{ width: '50%', height: 300 }}>
          <NWInCurrenciesTimelineChart stacked={false} />
        </div>
        <div style={{ width: '50%', height: 300 }}>
          <NWDiffInCurrenciesTimelineChart stacked={false} />
        </div>
      </div>
      {nwValuationAllocationTimelineBlock}
      {assetsAllocationTimelineBlock}
      {liabilitiesAllocationTimelineBlock}
      <div
        style={{
          width: '100%',
          height: '200px',
          marginBottom: '300px',
          display: 'flex',
        }}
      >
        <div style={{ width: '100%', height: '300px' }}>
          <NWValuationAllocationChart />
        </div>
      </div>
      <div
        style={{
          width: '100%',
          height: '300px',
          marginTop: '300px',
          marginBottom: '200px',
          display: 'flex',
        }}
      >
        <AssetClassValuationAllocationPieCharts />
      </div>
    </div>
  );
};
