import React, { useContext } from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { Link } from 'react-router-dom';
import { Button, Grid } from '@material-ui/core';
import { Sort } from '@material-ui/icons';

import { StoreContext } from 'src/App';
import { getAssetAccounts, getInstitutions } from 'src/Home';
import { ContentContainer } from 'src/components/ContentContainer';

export const Insitutions: React.FC = () => {
  const store = useContext(StoreContext);

  return (
    <ContentContainer>
      <Grid container spacing={1}>
        <Grid item>
          <Button
            startIcon={<Sort />}
            to="sort"
            component={Link}
            variant="contained"
            color="secondary"
            size="small"
          >
            sort
          </Button>
        </Grid>
        <Grid item>
          <Button
            to="create"
            component={Link}
            variant="contained"
            color="secondary"
            size="small"
          >
            add
          </Button>
        </Grid>
      </Grid>

      <Table stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell>type</TableCell>
            <TableCell>name</TableCell>
            <TableCell>used in</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {getInstitutions(store)?.map(inst => {
            const assetAccountsWithCurrentInstitution = getAssetAccounts(
              store
            )!.filter(aa => aa.institutionId === inst.id);
            return (
              <TableRow hover key={inst.id} component={Link} to={inst.id}>
                <TableCell>{inst.type}</TableCell>
                <TableCell>{inst.name}</TableCell>
                {/* <TableCell>{inst.sortIndex}</TableCell> */}
                <TableCell>
                  {assetAccountsWithCurrentInstitution.length}
                </TableCell>
                {/* <TableCell>
                    {assetAccountsWithCurrentInstitution.length === 0 && (
                      <button
                        type="button"
                        onClick={() => deleteInstitution(inst.id)}
                      >
                        delete
                      </button>
                    )}
                  </TableCell> */}
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </ContentContainer>
  );
};
