import React, { useContext, useEffect } from 'react';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import _ from 'lodash';

import {
  StoreContext,
  UserConfigContext,
  ReportCurrencyContext,
} from 'src/App';
import { getTotalForReport } from 'src/Home';
import { ChartDataPointsPerCategory, configureTimelineChart } from './utils';

function createChart(
  name: string,
  containerId: string,
  dataPerCategory: ChartDataPointsPerCategory[],
  stacked: boolean
) {
  let chart = am4core.create(containerId, am4charts.XYChart);

  // Title
  let chartTitle = chart.titles.push(new am4core.Label());
  chartTitle.text = name;
  chartTitle.fontSize = 25;

  configureTimelineChart(chart, !stacked);
  _.map(dataPerCategory, (seriesData, index) => {
    let series = chart.series.push(new am4charts.LineSeries());
    series.data = seriesData.points;
    series.dataFields.dateX = 'date';
    series.dataFields.valueY = 'value';
    series.dataFields.valueYShow = 'previousChange';
    series.name = seriesData.category;
    series.strokeWidth = 3;
    series.tensionX = 0.8;
    series.tooltipText = '{valueY.previousChange}';

    const bullet = new am4charts.CircleBullet();
    series.bullets.push(bullet);

    if (stacked) {
      let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());

      let range = valueAxis.createSeriesRange(series);
      range.value = 0;
      range.endValue = -100000000;
      range.contents.stroke = am4core.color('#f00');
      range.contents.fill = am4core.color('#f00');
      range.contents.fillOpacity = 0.2;

      series.yAxis = valueAxis;
      valueAxis.renderer.line.strokeOpacity = 1;
      valueAxis.renderer.line.stroke = series.stroke;
      valueAxis.renderer.grid.template.stroke = series.stroke;
      valueAxis.renderer.grid.template.strokeOpacity = 0.1;
      valueAxis.renderer.labels.template.fill = series.stroke;
      valueAxis.renderer.minGridDistance = 20;
      valueAxis.align = 'right';
      if (index === 0) {
        valueAxis.marginBottom = 20;
      } else {
        if (index === dataPerCategory.length - 1) {
          valueAxis.marginTop = 20;
        } else {
          valueAxis.marginTop = 10;
          valueAxis.marginBottom = 10;
        }
      }
    }
    // else {
    //   _.forEach(chart.yAxes.values, (axes) => {
    //     const axesY = axes as unknown as am4charts.ValueAxis<am4charts.AxisRenderer>;
    //     let range = axesY.createSeriesRange(series);
    //     range.value = 0;
    //     range.endValue = -100000000;
    //     range.contents.stroke = am4core.color("#f00");
    //     range.contents.fill = am4core.color("#f00");
    //     range.contents.fillOpacity = 0.2;
    //   });
    return series;
  });

  if (stacked) {
    chart.leftAxesContainer.layout = 'vertical';
  }

  return chart;
}

export const NWDiffInCurrenciesTimelineChart: React.FC<{
  stacked: boolean;
}> = ({ stacked }) => {
  const containerId = 'nw_diff_in_currencies_timeline_chart';
  const store = useContext(StoreContext);
  const currencies = [useContext(ReportCurrencyContext)];
  // const userConfig = useContext(UserConfigContext);
  // const currencies = userConfig.reportCurrencies;
  const reports = store.data.reports;

  const dataPerCurrency: ChartDataPointsPerCategory[] = currencies.map(
    currency => {
      const totalsInCurrency = _.compact(
        reports?.map(report => {
          return {
            date: new Date(report.datetime),
            value: getTotalForReport(store, report, currency).toFixed(0),
          };
        })
      );
      return {
        category: currency,
        points: totalsInCurrency || [],
      };
    }
  );

  useEffect(() => {
    const chart = createChart(
      'Net worth valuation in currency diff',
      containerId,
      dataPerCurrency,
      stacked
    );
    return () => {
      chart.dispose();
    };
  }, [dataPerCurrency, containerId, stacked]);

  return (
    <div
      id={containerId}
      style={{ width: '100%', height: '500px', marginBottom: '100px' }}
    ></div>
    // <div id="selectordiv" style={{ width: "100%", height: "30px", marginBottom: "100px" }} />
  );
};
