function getExchangeRate(
  store: fintrack.JSONShape,
  erData: fintrack.ExchangeRateData | undefined,
  // report: fintrack.Report,
  asset: fintrack.Asset,
  targetCurrency: fintrack.Currency
): Big {
  if (erData === undefined) {
    return new Big(0);
  }
  const isBaseAssetACurrency = asset.class === 'CURRENCY';
  // if (asset.currency === targetCurrency) {
  //   return 1;
  // }

  // then find such asset where class is currency, name is asset.currency
  // and check whether currency of such asset is `fintrack.BaseCurrency` (USD)
  const currencyAsset = isBaseAssetACurrency
    ? asset
    : store.data.assets.find(
        a => a.class === 'CURRENCY' && a.name === asset.currency
      )!;
  let erToBaseCurrency: Big = new Big(1);

  if (currencyAsset.currency !== targetCurrency) {
    const currencyAssetToBase = store.data.assets.find(
      a => a.class === 'CURRENCY' && a.name === targetCurrency
    );
    if (currencyAssetToBase !== undefined) {
      const er = erData[currencyAssetToBase.id];
      if (er !== null) {
        erToBaseCurrency = new Big(er!);
      } else {
        erToBaseCurrency = new Big(0);
      }
    }
  }
  if (erToBaseCurrency.eq(0)) {
    return new Big(0);
  }
  return new Big(erData[currencyAsset.id] || 0)
    .div(erToBaseCurrency)
    .mul(isBaseAssetACurrency ? new Big(1) : new Big(erData[asset.id] || 0));
}

export function getNonZeroAssetClasses({
  store,
  report,
  assetFilterFunction,
  mode,
}: {
  store: fintrack.JSONShape;
  report?: fintrack.Report;
  assetFilterFunction?: (asset: fintrack.Asset) => boolean;
  mode?: 'assets' | 'liabilities';
}): fintrack.ASSET_TYPE[] {
  let classes: fintrack.ASSET_TYPE[] = [];

  let states = store.data.assetAccountStates;
  if (report !== undefined) {
    states = states.filter(aas => aas.reportId === report.id);
  }
  states.forEach(accState => {
    const balance = parseFloat(accState.balance);
    let balanceMatches = false;
    switch (mode) {
      case undefined:
        balanceMatches = balance !== 0;
        break;
      case 'assets':
        balanceMatches = balance > 0;
        break;
      case 'liabilities':
        balanceMatches = balance < 0;
        break;
    }
    if (balanceMatches) {
      const account = store.data.assetAccounts.find(
        aa => aa.id === accState.assetAccountId
      )!;
      // find and maybe skip asset
      const asset = store.data.assets.find(a => a.id === account.assetId)!;
      if (assetFilterFunction !== undefined && !assetFilterFunction(asset)) {
        // skip
      } else {
        classes.push(asset.class);
      }
    }
  });
  return _.uniq(classes);
}

export function getNonZeroAssetClassNames({
  store,
  report,
  assetClass,
}: {
  store: fintrack.JSONShape;
  report: fintrack.Report;
  assetClass: fintrack.ASSET_TYPE;
}): string[] {
  let names: string[] = [];

  let states = store.data.assetAccountStates.filter(
    aas => aas.reportId === report.id
  );
  states.forEach(accState => {
    if (parseFloat(accState.balance) !== 0) {
      const account = store.data.assetAccounts.find(
        aa => aa.id === accState.assetAccountId
      )!;
      const asset = store.data.assets.find(a => a.id === account.assetId)!;
      if (asset.class === assetClass) {
        names.push(asset.name);
      }
    }
  });
  return _.uniq(names);
}

// TODO: compute once after store data update and use in all components?
export function hadLiabilities({
  store,
  assetFilterFunction,
}: {
  store: fintrack.JSONShape;
  assetFilterFunction?: (asset: fintrack.Asset) => boolean;
}): boolean {
  let hadLiabilities = false;
  store.data.assetAccountStates.forEach(accState => {
    const account = store.data.assetAccounts.find(
      aa => aa.id === accState.assetAccountId
    )!;
    // find and maybe skip asset
    const asset = store.data.assets.find(a => a.id === account.assetId)!;
    if (assetFilterFunction !== undefined && !assetFilterFunction(asset)) {
      // skip
    } else {
      if (!hadLiabilities) {
        hadLiabilities = parseFloat(accState.balance) < 0;
      }
    }
  });
  return hadLiabilities;
}

export function getTotalForReport(
  store: fintrack.JSONShape,
  report: fintrack.Report,
  targetCurrency: fintrack.Currency,
  assetFilterFunction?: (asset: fintrack.Asset) => boolean,
  accountStateFilterFunction?: (account: fintrack.AssetAccountState) => boolean
): Big {
  return store.data.assetAccountStates
    .filter(aas => aas.reportId === report.id)
    .reduce((acc, currAccountState) => {
      // maybe skip account state
      if (
        accountStateFilterFunction !== undefined &&
        !accountStateFilterFunction(currAccountState)
      ) {
        return acc;
      }

      const account = store.data.assetAccounts.find(
        aa => aa.id === currAccountState.assetAccountId
      )!;

      // find and maybe skip asset
      const asset = store.data.assets.find(a => a.id === account.assetId)!;
      if (assetFilterFunction !== undefined && !assetFilterFunction(asset)) {
        return acc;
      }

      // asset.currency
      const erData = store.data.exchangeRates.find(
        er => er.reportId === report.id
      )?.data;
      // const erToAssetCurrency = erData[asset.id] ?? '0';
      let erFromAssetToTarget = getExchangeRate(
        store,
        erData,
        asset,
        targetCurrency
      );
      const assetPriceInBaseCurrency =
        // parseFloat(erToAssetCurrency) *
        erFromAssetToTarget.mul(new Big(currAccountState.balance));
      return assetPriceInBaseCurrency.plus(acc);
    }, new Big(0));
}

export function getInstitutions(
  store: fintrack.JSONShape
): fintrack.Institution[] | undefined {
  return _.sortBy(store.data.institutions, i => i.sortIndex);
}

export function getAssetAccounts(
  store: fintrack.JSONShape
): fintrack.AssetAccount[] | undefined {
  return getInstitutions(store)?.flatMap(inst => {
    return _.sortBy(
      store.data.assetAccounts.filter(aa => aa.institutionId === inst.id),
      // TODO replace name with sort order
      aa => aa.name
    );
  });
}

